import React, {useState, useEffect} from "react";
import axios from 'axios';

axios.defaults.baseURL = 'http://localhost:8080';

export function LoginContainer() {

    let [isLogin, loginStatus] = useState(false);
    let [user, setUser] = useState('');
    let [passwd, setPasswd] = useState('');
    let [userName, setName] = useState('')
    let [userSession, setSession] = useState('')
    let [datosCorrectos, setBadLogin] = useState(false)

    useEffect(() => {
        console.log('init');
        status();

    },[]);

    async function login(){
        
        console.log(document.cookie);

        setBadLogin(false)
        
        axios.get(`/login/?user=${user}&pass=${passwd}`, {withCredentials: true})
        .then(res => {
            setSession(res.data.idSession)
            loginStatus(true)
            setUser('')
            setPasswd('')
            console.log(document.cookies.connect.sid);
        })
        .catch(err => {
                setBadLogin(true)
        })

        return false
    }
    
    async function status(){
        
        axios.get('/status', {withCredentials: true})
        .then(res => {
            loginStatus(true)
            setSession(res.data.idSession)
            console.log(res.data.mensaje, res.data.idSession);
        })
        .catch(err => {
            loginStatus(false)
        })

        return false
    }

    async function logout(){
        axios.get('/logout', {withCredentials: true})
        .then(res => {

            loginStatus(false)
        })
        .catch(console.log)
    }

    function updateUser(event){
        setUser(event.target.value)
    }

    function updatePasswd(event){
        setPasswd(event.target.value)
    }

    return (
        <React.Fragment>
            {isLogin ? 
            
            <React.Fragment>
                <div className="m-4 p-4 bg-primary">
                    <h1>{userName}</h1>
                    <h2>Su id de sesion es: {userSession}</h2>
                    <button onClick={()=>status()} className="mt-3">Actualizar productos</button>
                    <button onClick={()=>logout()} className="mt-3">Desloguear</button>
                </div>
            </React.Fragment>

            :
            <React.Fragment>     
                <React.Fragment>
                <form className="m-4 p-4 bg-primary" onSubmit={()=>login()}>
                    <fieldset>
                        <div className="form-group">
                            <label>Usuario</label>
                            <input type="email" className="form-control" id="loginUser" aria-describedby="emailHelp" placeholder="ingresa usuario" onChange={updateUser.bind(this)}/>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input type="password" className="form-control" id="loginPassword" placeholder="Password" onChange={updatePasswd.bind(this)} />
                        </div>
                    </fieldset>
                    <button onClick={e => {e.preventDefault(); login()}} className="btn btn-primary">Ingresar</button>
                </form>
                <React.Fragment>
                {!datosCorrectos ?
                <div></div>
                :
                <div style={{color: "red"}}>Datos Incorrectos</div>
                }
                </React.Fragment>
                </React.Fragment>
            </React.Fragment>
            }
        </React.Fragment>
    );
 
}
