import React, {useState, useEffect} from 'react'
import { ItemList } from "../productList/productList";




export function ItemListContainer() {
        
    let [productList, setProductList] = useState([]);


    useEffect(()=>{
      fetch('http://localhost:8080/productos/')
      .then(res => res.json())
      .then((data) => {
        console.log(data);
        setProductList(data)
      })
      .catch(console.log)
    },[])

    return (
      <React.Fragment>
        { productList && productList.length ? 
            <React.Fragment>
              <h1>Coleccion 2021</h1>
              <div className="row justify-content-center">
                <ItemList productsToShow={productList}/>
              </div>
            </React.Fragment>
          :
          <div>Cargando productos</div> 
        }   
      </React.Fragment>
    );
}





