import React from 'react'
import {Product} from '../product/product'


export function ItemList({productsToShow}) {

    return (
        <React.Fragment>
            {productsToShow.map((i)=><div key={i._id} className="card" style={{width: 15+"rem", margin: "10px"}}> <Product item={i}/></div>)}
        </React.Fragment>
    );
}